.backdrop {

    position: absolute;
    z-index: 11;
    
    display: flex;
    align-items: center;
    justify-content: center;

    left: 0;
    top: 0;

    width: 100vw;
    height: 100vh;

    background-color: rgba(128, 128, 128, 0.7);

    .container {

        display: flex;
        flex-direction: column;
        align-items: center;
        // justify-content: center;

        width: 400px; 
        // height: fit-content;

        padding: 0 0 20px 0;

        border-radius: 8px;
        background-color: white;

    };

};
