.container {

    // width: 100%;
    
    height: 100vh;

    padding: 0 15px;
    background-color: lightgrey;

    // overflow-y: scroll;

    .loadContainer {

        display: flex;
        justify-content: center;
        align-items: center;

        height: 10px;
        margin-bottom: 5px;
    }

    .driveContainer {

        display: flex;
        justify-content: space-between;
        align-items: center;

        height: 10px;
        margin-bottom: 5px;

    }

    .title {
        color: white;
        font-weight: 600;
        margin-bottom: 5px;
    }
    
    .arts {

        display: flex;
        height: 25px;
        gap: 0;

        border: none;
        border-bottom: 2px solid white;

        button {
            width: calc(100%/3);
            height: 100%;

            border-radius: 0;
            border: 2px solid white;
            border-top: none;
            border-right: none;
            border-left: none;
            border-bottom: none;
            color: rgb(82, 82, 82);
            font-weight: 600;
        } 

        .artIcon {
            
            display: flex;
            justify-content: space-around;
            align-items: center;

        }
    }

    .style {

        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        
        row-gap: 10px;
        column-gap: 10px;
        
        padding: 20px 10px;
        margin: 0;

        list-style: none;

        border: none;
        border-radius: 6px 6px 0 0;
        border-bottom: 2px solid white;
        box-shadow: inset 1px 1px 4px 3px rgba(16, 16, 24, 0.08);

        button {
            width: 100px;
            height: 20px;

            border: none;
            border-radius: 6px;

            color: rgb(82, 82, 82);
            font-weight: 600;

            cursor: pointer;
        } 
        
        button:hover {

            background-color: rgba(194, 212, 31, 0.801);

        }
        
    }

    .total {

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        margin-bottom: 5px;

        font-weight: 600;

        color: white;

        border-bottom: 2px solid white;

        .selectall {

            // width: fit-content;

            margin: 5px 0 5px 0;

            padding: 5px;

            border-radius: 6px;

            box-shadow: 1px 1px 4px 3px rgba(16, 16, 24, 0.08);

            cursor: pointer;

        }

        .selectall:active {

            box-shadow: inset 1px 1px 4px 3px rgba(16, 16, 24, 0.08);

        }
    }

    .listItems {

        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        column-gap: 10px;
        row-gap: 10px;

        height: 50vh;

        list-style: none;
        padding: 5px 0;
        margin: 0;

        border-top: 3px solid #1C274C;
        border-bottom: 3px solid #1C274C;

        box-shadow: inset 1px 1px 4px 3px rgba(16, 16, 24, 0.08);

        background-image: url('../../images/bubble-svgrepo-com.svg');

        overflow-y: scroll;

        &::-webkit-scrollbar {
           width: 12px;
           box-shadow: inset 1px 1px 4px 3px rgba(16, 16, 24, 0.2);
           background-color: lightgray;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgb(122, 152, 206);
            border-left: 1px solid rgb(128, 128, 128, 0.3);
            border-right: 1px solid rgb(128, 128, 128, 0.3);
            border-radius: 6px;
            box-shadow: inset 1px 1px 3px 1px rgba(208, 208, 255, 0.884);
        }

        .item {
            
            width: calc(100% / 1.7);
            // height: calc(width * 2);
            
            border-radius: 6px;
            background-color: white;
            
            padding: 20px 5px;

            cursor: pointer;

            box-shadow: inset 0 0 7px #b6b5b5, 0px 2px 1px rgba(16, 16, 24, 0.08), 0px 1px 1px rgba(46, 47, 66, 0.16), 0px 1px 6px rgba(46, 47, 66, 0.08);
        }

        .item:hover {
            outline: 2px solid #1C274C;
        }

        .blotImg {

            width: 60%;
            height: 60%;

            fill: gray;
        }

        .notResult {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            gap: 20px;
        }

    }

    .modalItemContainer {
        
        img {
            width: 100%;
        };
        
    }

    .content {
        overflow: scroll;  
    }
    
}

.statitem {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    // width: 100%;
    margin-bottom: 10px;

    .statpoint {
        display: flex;
        flex-direction: column;
        justify-content: space-around;  
        align-items: center;

        gap: 5px;
    }

    .hearts:hover {
        cursor: pointer;
    }
}
//******************************@media rules************************************

//-------------------(min-width: 768px) and (max-width: 1439px)-----------------
@media screen and (min-width: 768px) and (max-width: 1439px) {
    .container {
       
       position: initial;
       width: unset;

       padding: 0 20px;

       .style {

        justify-content: initial;

       }

       .listItems {

        width: unset;
        height: 65vh;

          .item {
            width: calc((100% / 3) - 20px);
            margin: 0;
          }
       }

       
    }

    .content {

        .statitem {
            display: flex;
            flex-direction: row;
            justify-content: space-around;

            width: 100%;
            margin-bottom: 10px;

            .statpoint {
                display: flex;
                flex-direction: row;
                justify-content: space-around;  
                align-items: center;
        
                gap: 5px;
            }
        }
    }
}

//-------------------min-width: 1440px----------------------------
@media screen and (min-width: 1440px) {
    .container {
      
        position: initial;
        width: unset;

        padding: 0 20px;

        .style {

            justify-content: initial;
            
        }

        .listItems {

            width: unset;
            height: 65vh;

            .item {
                width: calc((100% / 3) - 20px);
                margin: 0;
            }
        }
    }

    .content {

        .statitem {
            display: flex;
            flex-direction: row;
            justify-content: space-around;

            width: 100%;
            margin-bottom: 10px;

            .statpoint {
                display: flex;
                flex-direction: row;
                justify-content: space-around;  
                align-items: center;
        
                gap: 5px;
            }
        }
    }
}
//******************************end************************************